import React from "react"
import { render } from "react-dom"
import { Router, Link } from "@reach/router"
import StatusPage from "./routes/status"

let Home = () => <div>Home</div>
let Dash = () => <div>Dash</div>
function App() {
  return (
    <Router>
    <Home path="/" />
    <StatusPage path="/status/:mail"/>
    <Dash path="dashboard" />
  </Router>
  );
}

export default App;
