import React from "react"
import "./index.css"
import moment from "moment"
export default function StatusPage(props) {

const ImageServiceURL = "https://hexatown-nets.azurewebsites.net/api/BookingImages?code=xfwn2Q9MMrVoBNr4/JUM8qCkAPCgpcVgJE7QJbaaKvogd10CSgtfOw=="


var date =moment().format('YYYY-MM-DD'); 

var img = ImageServiceURL + "&show=room&mail=" + props.mail + "&date=" + date


  return (
<div>
    <div>
    
    <img className="statusImage"  src={img}/>
    </div>
</div>
  );
}

